import { Box, Container, Grid, Paper, TextField, Typography, Button, IconButton, Avatar } from "@mui/material";
import CalendarTodayIcon from '@mui/icons-material/CalendarToday';
import { useState } from 'react';
import { DatePicker } from '@mui/x-date-pickers';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { LocalizationProvider } from '@mui/x-date-pickers';
import axios from "axios";
import { useIsLoginState } from "./authContext";
import { useNavigate } from "react-router-dom";

// Sample avatars
const avatars = [
    'https://bff-images.bemypet.kr/media/medias/all/993-image_picker152967371293908462.jpg',
    'https://t4.daumcdn.net/thumb/R720x0.fjpg/?fname=http://t1.daumcdn.net/brunch/service/user/2fG8/image/NZ9EznOU5Dng8VHBzobu3zqzyIU.jpg',
    'https://preview.free3d.com/img/2018/04/2279509641907930122/ukq9qttq.jpg'
];

function Create() {
    // 
    const navigate = useNavigate();

    //   
    const isLogin = useIsLoginState();

    //    
    const [date, setDate] = useState(null);
    const [selectedAvatar, setSelectedAvatar] = useState(null);
    const [userId, setUserId] = useState("");
    const [password, setPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");
    const [nickname, setNickname] = useState("");
    const [fullName, setFullName] = useState("");
    const [userEmail, setEmail] = useState("");
    const [passFlag , setFlag ] = useState(false);


    // console.log("date : ",  Date.UTC(date, 'yyyy-MM-dd') )
    // console.log("selectedAvatar",selectedAvatar )
    // console.log("userId",userId )
    // console.log("password",password )
    // console.log("confirmPassword",confirmPassword )
    // console.log("nickname",nickname )
    // console.log("fullName",fullName )
    // console.log("userEmail",userEmail )


    //  
    const [errors, setErrors] = useState({
        userId: "",
        password: "",
        confirmPassword: "",
        fullName:"",
        email:""

    });

    //        
    const EnrollChange = (e) => {
        const { id, value } = e.target;

        //  
        const userIdRegex =  /^[a-zA-Z0-9]{6,20}$/; //   6 20  .
        const passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{6,30}$/; //   , ,   , 8 30  .
 
        switch (id) {
            case "userId":
                setUserId(value);
                if (!userIdRegex.test(value)) {
                    setErrors((prev) => ({ ...prev, userId: " 6 20  ." }));
                }
                else {
                    setErrors((prev) => ({ ...prev, userId: "" }));
                }
                break;
            case "password":
                setPassword(value);
                if (!passwordRegex.test(value)) {
                    setErrors((prev) => ({ ...prev, password: "   , ,   , 6 30  ." }));
                } else {
                    setErrors((prev) => ({ ...prev, password: "" }));
                }
                break;
            case "confirmPassword":
                setConfirmPassword(value);
                if (value !== password) {
                    setErrors((prev) => ({ ...prev, confirmPassword: "  ." }));
                } else {
                    setErrors((prev) => ({ ...prev, confirmPassword: "" }));
                }
                break;
            case "nickname":
                setNickname(value);
                break;
            case "fullName": //   
                setFullName(value);
                if (value.trim() === "") {
                    setErrors((prev) => ({ ...prev, fullName: " ." }));
                } else {
                    setErrors((prev) => ({ ...prev, fullName: "" }));
                }
                break
            case "outlined-email-input": //   
                setEmail(value);
                if (value.trim() === "") {
                    setErrors((prev) => ({ ...prev, email: " ." }));
                } else {
                    setErrors((prev) => ({ ...prev, email: "" }));
                }
                break;
            default:
                break;
        }
        
    };

    return (
        <>
      {  !isLogin?
      //     
       <Container maxWidth="md" sx={{ mt: 6 , height:'100%'}}>
                <Grid container justifyContent="center">
                    <Paper elevation={2} sx={{
                        p: 4,
                        borderRadius: 3,
                        boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)',
                        display: 'flex',
                        flexDirection: 'column',
                        bgcolor: '#f5f5f5',
                        border: '1px solid #ddd',
                        width: '800px',
                    }}>
                        <Box>
                            <Typography variant="h4" color="primary" sx={{ textAlign: "center" , mb:2}}>
                                 
                            </Typography>
                        </Box>

                        <Box sx={{ mt: 2, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                            {/* Avatar selection */}
                            <Box sx={{ mb: 9 }}>
                                <Grid container spacing={2} justifyContent="center" sx={{ mt: 2 }}>
                                    {avatars.map((avatar, index) => (
                                        <Grid item key={index}>
                                            <Avatar
                                                src={avatar}
                                                sx={{
                                                    width: 100,
                                                    height: 100,
                                                    border: selectedAvatar === avatar ? '3px groove #FE2E2E' : 'none',
                                                    cursor: 'pointer',
                                                    '&:hover': {
                                                        boxShadow: '0px 8px 12px rgba(0, 0, 0, 0.7)'
                                                    }
                                                }}
                                                onClick={() => setSelectedAvatar(avatar)}
                                            />
                                        </Grid>
                                    ))}
                                </Grid>
                                <Typography variant="h6" sx={{ textAlign: 'center' , mt:3}}>  .</Typography>
                            </Box>
                             {/*    */}
                            <TextField
                                id="fullName"
                                label=" "
                                type="text"
                                value={fullName}
                                onChange={EnrollChange}
                                sx={{ width: '100%', maxWidth: '400px', mb: 4 }}
                                error={!!errors.fullName}
                                helperText={errors.fullName}
                                required
                            />                         
                            <Box  sx={{display:'flex', maxWidth:'400px'}}>
                            <TextField
                                id="userId"
                                label=" "
                                type="text"
                                value={userId}
                                onChange={EnrollChange}
                                sx={{ width: '400px', maxWidth: '400px', mb: 4 }}
                                error={!!errors.userId}
                                helperText={errors.userId}
                            />

                            <Button variant="outlined" sx={{width:'105px',height:'56px',ml:5}}
                                onClick={()=>{
                                    axios.post('/user/userIdCheck',{userId:userId}).then(result => {
                                        console.log("   : ", result.data);
                                        if(!result.data){
                                            setFlag(true);
                                            alert(" .")
                                        }else{
                                            setErrors((prev) => ({ ...prev, userId: "  " }));
                                        }
                                    }
                                    )   
                                }}
                            >
                            <Typography sx={{fontSize:'12px'}}> </Typography>
                            </Button>
                            </Box>

                            <TextField
                                id="password"
                                label=" "
                                type="password"
                                value={password}
                                onChange={EnrollChange}
                                sx={{ width: '100%', maxWidth: '400px', mb: 4 }}
                                error={!!errors.password}
                                helperText={errors.password}
                            />

                            <TextField
                                id="confirmPassword"
                                label=" "
                                type="password"
                                value={confirmPassword}
                                onChange={EnrollChange}
                                sx={{ width: '100%', maxWidth: '400px', mb: 4 }}
                                error={!!errors.confirmPassword}
                                helperText={errors.confirmPassword}
                            />

                            <TextField
                                id="nickname"
                                label=" "
                                type="text"
                                value={nickname}
                                onChange={EnrollChange}
                                sx={{ width: '100%', maxWidth: '400px', mb: 4 }}
                            />
                            <TextField
                                id="outlined-email-input"
                                label=""
                                type="text"
                                value={userEmail}
                                onChange={EnrollChange}
                    
                                sx={{ width: '100%', maxWidth: '400px', mb: 4 }}  // Adjust width and spacing
                            />

                            {/* Date of Birth */}
                            <Box sx={{ mb: 4, width: '100%', maxWidth: '400px' }}>
                                <LocalizationProvider dateAdapter={AdapterDateFns}>
                                    <DatePicker
                                       
                                        label=""
                                        value={date}
                                        onChange={(newValue) => setDate(newValue)}
                                        renderInput={(params) => (
                                            <TextField
                                                {...params}
                                                fullWidth
                                                sx={{ width: '100%' }}
                                                InputProps={{
                                                    ...params.InputProps,
                                                    endAdornment: (
                                                        <>
                                                            {params.InputProps.endAdornment}
                                                            <IconButton sx={{ ml: 1 }} aria-label="calendar" component="span">
                                                                <CalendarTodayIcon />
                                                            </IconButton>
                                                        </>
                                                    ),
                                                }}
                                            />
                                        )}
                                    />
                                </LocalizationProvider>
                            </Box>

                            <Box sx={{ display: 'flex', justifyContent: 'center', width: '100%' }}>
                                <Button                                    
                                    variant="outlined"
                                    color="primary"
                                    sx={{
                                        mt: 2,
                                        mb: 2,
                                        width: '150px',
                                    }}
                                    onClick={()=>{
                                        if(passFlag){
                                            if(date !=null && selectedAvatar != null && userId != "" && password != "" && confirmPassword != "" && nickname !="" && fullName != "" && userEmail != ""){
                                            console.log("");
                                            (axios.post('/user/enrollUser',{
                                                userId:userId,
                                                userPwd:password,
                                                userName:fullName,
                                                nickName:nickname,
                                                userBirth:date,
                                                email:userEmail,
                                                imgPath:selectedAvatar
                                            }).then(result =>{
                                                console.log(result)
                                                if(result.data){
                                                    alert("   .")
                                                    navigate('/')
                                                }else{
                                                    alert("  .")
                                                    navigate('/enroll')
                                                }
                                            }).catch(result =>{
                                                console.log("here : ",result)
                                                if(result.response.data.status === 500){
                                                    console.log("")
                                                }

                                            }))
                                            
                                        }else{
                                              // alert(errors)
                                              alert("    ");
                                        } 
                                        }else{
                                           alert("   !");
                                         }
                                    }
                                    }
                                >
                                    
                                </Button>
                            </Box>
                        </Box>
                    </Paper>
                </Grid>
            </Container> :
            
            //   
            <Container>
                    <Box  sx={{ mt: 2, display: 'flex', flexDirection: 'column', alignItems: 'center'  }}>
                           .
                    </Box>
            </Container>
            
            
            }
        </>
    );
}

export default Create;
