import * as React from 'react';
import { useEffect, useState } from 'react';
import axios from 'axios';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

function AboutMe() {

  const [userInfo, setUserInfo] = useState({
    userAddress: '', //   
    //     
  });

  //    
  const fetchUserInfo = async () => {
    try {
      //  API   
      const response = await axios.get(`/user/getuserInfo/${sessionStorage.getItem("id")}`); // ID  
      setUserInfo(response.data);
    } catch (error) {
      console.error('Error fetching user info:', error);
    }
  };

  //      
  useEffect(() => {
    fetchUserInfo();
  }, []);

  //    (11/11/11 )
  const formatBirthDate = (birthDate) => {
    if (!birthDate) return ' ';
    const date = new Date(birthDate);
    return date.toLocaleDateString('ko-KR', {
      year: '2-digit',
      month: '2-digit',
      day: '2-digit',
    });
  };

  //    (23/02/02 )
  const formatJoinDate = (joinDate) => {
    if (!joinDate) return ' ';
    const date = new Date(joinDate);
    return date.toLocaleDateString('ko-KR', {
      year: '2-digit',
      month: '2-digit',
      day: '2-digit',
    });
  };

  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650}} aria-label="user info table">   
        <TableBody>
          <TableRow>
            <TableCell></TableCell>
            <TableCell align="left">{userInfo.userName || ' '}</TableCell>
          </TableRow>
          <TableRow>
            <TableCell></TableCell>
            <TableCell align="left">{userInfo.userId || ' '}</TableCell>
          </TableRow>
          <TableRow>
            <TableCell></TableCell>
            <TableCell align="left">{formatBirthDate(userInfo.userBirth)}</TableCell>
          </TableRow>
          <TableRow>
            <TableCell></TableCell>
            <TableCell align="left">{userInfo.email || ' '}</TableCell>
          </TableRow>
          <TableRow>
            <TableCell></TableCell>
            <TableCell align="left">{formatJoinDate(userInfo.createAt)}</TableCell>
          </TableRow>
          <TableRow>
            <TableCell></TableCell>
            <TableCell align="left">{userInfo.userAddress || ' '}</TableCell>
          </TableRow>
        </TableBody>
      </Table>
    </TableContainer>
  );
}

export default AboutMe;
