import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { TextField, Button, Box, Typography, Container } from '@mui/material';

function EditMe() {
  const [userInfo, setUserInfo] = useState({
    nickName: '',
    email: '',
    password: ''
  });

  const [originalInfo, setOriginalInfo] = useState({});

  useEffect(() => {
    //      
    const fetchUserInfo = async () => {
      try {
        await axios.get(`/user/getuserInfo/${sessionStorage.getItem("id")}`).then((result)=>{
          console.log("axios getuserInfo Result : ",result );
          setOriginalInfo(result.data); //   
        }); // ID  
        
      } catch (error) {
        console.error('Error fetching user info:', error);
      }
    };

    fetchUserInfo();
  }, []);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setUserInfo({
      ...userInfo,
      [name]: value,
    });
  };

  const handleSubmit = async (field) => {
    if (!userInfo[field]) {
      alert("  ");
      return;
    }

    if (userInfo[field] === originalInfo[field]) {
      alert("  ");
      setUserInfo({
        ...userInfo,
        [field]: ''  //     
      });
      return;
    }

    try {
      const response = await axios.post(`/user/updateUserInfo`, { userId: sessionStorage.getItem('id'), field, value: userInfo[field] });
      if (response.data.success) {
        alert(" ");
        setOriginalInfo({ ...originalInfo, [field]: userInfo[field] });

        //   
        setUserInfo({
          ...userInfo,
          [field]: ''  //     
        });

        //  
        window.location.reload(); //    
      } else {
        alert(" .");
      }
    } catch (error) {
      console.error("Error updating user info:", error);
    }
  };

  const handleReset = () => {
    setUserInfo({
      nickName: '',
      email: '',
      password: ''
    });
  };

  return (
    <Container sx={{width:'70%'}}>
      <Typography variant="h4" component="h2" gutterBottom  color="primary" sx={{mt :8 , textAlign:'center'}}>
              
       </Typography>

      <Box sx={{ mb: 2, display: 'flex', alignItems: 'center', gap: 1 , mt:8}}>
        <TextField
          name="nickName"
          label=""
          variant="outlined"
          value={userInfo.nickName}
          onChange={handleInputChange}
          fullWidth
        />
        <Button onClick={() => handleSubmit('nickName')} variant="contained">
          
        </Button>
      </Box>

      <Box sx={{ mb: 2, display: 'flex', alignItems: 'center', gap: 1 ,mt:6}}>
        <TextField
          name="email"
          label=""
          type='email'
          variant="outlined"
          value={userInfo.email}
          onChange={handleInputChange}
          fullWidth
        />
        <Button onClick={() => handleSubmit('email')} variant="contained">
          
        </Button>
      </Box>

      <Box sx={{ mb: 2, display: 'flex', alignItems: 'center', gap: 1 ,mt:6}}>
        <TextField
          name="password"
          label=""
          type='password'
          variant="outlined"
          value={userInfo.password}
          onChange={handleInputChange}
          fullWidth
        />
        <Button onClick={() => handleSubmit('password')} variant="contained">
          
        </Button>
      </Box>
    </Container>

  );
}

export default EditMe;
